<template>
  
  <router-view  :count="count" :cart="cart" @add="add"></router-view>
</template>
<script>
import { onBeforeMount } from 'vue'
import { useStore } from 'vuex'
export default {
  name: 'App',
  methods: {
    add(product) {
      this.cart.push(product);
      this.count++;
      console.log(this.cart);
    }
  }, 
   setup() {
    const store = useStore()

    onBeforeMount(() => {
      store.dispatch('fetchUser')
    })
  }
  
}
</script>

